.right-side-login {
	justify-content: center;
	align-items: center;
	height: 100vh;
	background-color: #e5e1e11a;
	.login-form {
		width: 60%;
		height: 40%;
		padding: 20px;
		border-radius: 10px;
		justify-content: center;
		box-shadow: 0px 5px 7px rgba(0, 0, 0, 0.1);
		position: relative;
		.form-icon {
			font-size: 18px;
		}
		.form-title {
			position: absolute;
			top: -15%;
			left: 50%;
			transform: translateX(-50%);
			font-size: 30px;
			letter-spacing: 1px;
			font-family: Poppins-Bold;
		}
		input {
			border: none;
			border-bottom: 1px solid rgba(0, 0, 0, 0.1);
			border-radius: 0;
			outline: none;
			background-color: transparent;
			position: relative;
			transition: 0.2s ease;
			&:focus {
				border-bottom: 1px solid rgb(39, 70, 83);
			}
		}
		.login-btn {
			margin-top: 20px;
			border-radius: 10px;
			height: 45px;
			// background-color: #607d8b;
			background-image: url(../../../Assets/Images/Pattern-Randomized.svg);
			background-position: center;
			background-attachment: fixed;
			font-size: 18px;
			letter-spacing: 1px;
			color: white;
			position: relative;
			overflow: hidden;

			&::after {
				content: '';
				position: absolute;
				width: 100%;
				height: 100%;
				left: 0;
				top: 0;
				background-color: rgba(255, 255, 255, 0.068);
				transform: skewX(-20deg) translateX(-100%);
				transition: 0.4s ease-in-out;
			}
			&:hover::after {
				transform: translateX(0%);
			}
		}
	}
}
