.login-page {
	height: 100vh;
	width: 100vw;
	overflow: hidden;
	.left-side {
		flex-basis: 50%;
		background-image: url(../../Assets/Images/Pattern-Randomized.svg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		align-items: center;
		justify-content: center;
		.logo {
			font-size: 150px;
			text-align: center;
			font-family: Pacifico;
			color: white;
		}
	}
	.right-side {
		height: 200vh;
		flex-basis: 50%;
		position: relative;
		transition: 0.4s ease-in-out;
		transform: translateY(0%);
		&.show-signup {
			transform: translateY(-51%);
		}
		.toggle-forms-btn {
			width: 160px;
			height: 160px;
			border-radius: 50%;
			background: linear-gradient(to right, #f46b45, #eea849);
			position: absolute;
			bottom: 45%;
			left: 50%;
			transform: translateX(-50%);
			display: flex;
			flex-direction: column;
			align-items: center;
			z-index: 5;
			cursor: pointer;

			span {
				// padding-top: 20px;
				width: 100%;
				height: 50%;
				font-size: 22px;
				font-family: Pacifico;
				color: white;
				display: flex;
				align-items: center;
				justify-content: center;
				&:last-of-type {
					padding-bottom: 10px;
				}
			}
		}
		.right-side-signup {
			flex-basis: 50%;
			height: 100vh;
			position: relative;
		}
	}
}
